import React, { Component } from 'react';

class Project extends Component {
	constructor(props){
		super(props);
		this.state={
			projectData: props.project_value
		}
	}
	
	render(){
		let projectData=this.state.projectData;
		
		let id=projectData.id;
		let fixedarea=projectData.fixedarea;
		let {h3, span}=projectData.title;
		let {site, date, type, category}=projectData.content;
		let {image, alt}=projectData.thumbnail;
		let {renewal, structure, interactive, scm}=projectData.overview;
		let {github, classname, figma, figmaclassname}=projectData.link;
		let link=[];
		let {pcimage, pcalt}=projectData.pcphoto;
		let {subid, mobid}=projectData.sub;
		let mobileimageLi=[];
		
		if(figma){
			link.push(
				<div className="link" key={classname}>
					<a href={github} className={classname} target="portfolio"> </a>
					<a href={figma} className={figmaclassname} target="portfolio"> </a>
				</div>
			)
		}
		else{
			link.push(
				<div className="link" key={classname}>
					<a href={github} className={classname} target="portfolio"> </a>
				</div>
			)
		}
		for(let i=0;i<projectData.mobphoto.mobileimage.length;i++){
			let {mobimage, mobalt}=projectData.mobphoto.mobileimage[i];

			mobileimageLi.push(
				<li key={i+1}><img src={"./assets/images/"+mobimage} alt={mobalt} /></li>
			)
		}
		return(
			<div id={id} className="project">
				<div className="title_area">
					<div className="title">
						<a href="">
							<div className="ico"><i className="fas fa-play"></i></div>
							<div className="txt">
								<h3>{h3}</h3>
								<span>{span}</span>
							</div>
						</a>
					</div>
					<div className="content">
						<ul className="desc">
							<li>
								<span className="label">#SITE</span>
								<span>{site}</span>
							</li>
							<li>
								<span className="label">#DATE</span>
								<span>{date}</span>
							</li>
							<li>
								<span className="label">#TYPE</span>
								<span>{type}</span>
							</li>
							<li>
								<span className="label">#CATEGORY</span>
								<span>{category}</span>
							</li>
						</ul>
					</div>
				</div>
				<div className={fixedarea}></div>
				<div className="thumbnail">
					<img src={"./assets/images/"+image} alt={alt} />
				</div>
				<div className="overview_area">
					<div className="title">PROJECT OVERVIEW</div>
					<div className="content">
						<ul className="desc">
							<li>
								<span className="label">#RENEWAL SITE</span>
								<span>{renewal}</span>
							</li>
							<li>
								<span className="label">#STRUCTURE</span>
								<span>{structure}</span>
							</li>
							<li>
								<span className="label">#INTERACTIVE</span>
								<span>{interactive}</span>
							</li>
							<li>
								<span className="label">#SCM</span>
								<span>{scm}</span>
							</li>
						</ul>
					</div>
					<div className="link">
						{link}
					</div>
				</div>
				<div className="sub">
					<div id={subid} className="pc">
						<div className="inner">
							<div className="title">
								<span>DESKTOP DESIGN</span>
								<p>       <br className="br" /> .</p>
							</div>
							<div className="photo">
								<img src={"./assets/images/"+pcimage} alt={pcalt} />
							</div>
						</div>
					</div>
					<div id={mobid} className="mobile">
						<div className="inner">
							<div className="title">
								<span>MOBILE DESIGN</span>
								<p>  PC     .<br /> PC        .</p>
							</div>
							<div className="photo clearfix">
								<ul>
									{mobileimageLi}
								</ul>
							</div>
						</div>
					</div>
				</div>
			</div>
			
		);
	}
}

export default Project;