import React, { Component } from 'react';
import './css/style1.css';
import UIScript from './UIScript';



class App extends Component {
	constructor() {
		super();
		this.state={
			error: null,
			isLoaded: false,
			data: {}
		}
	}
	componentDidMount() {
		fetch("./data/UIScript_data.json")
			.then(response => response.json())
			.then(
				(result) => {
					this.setState({
						isLoaded: true,
						data: result
					});
				},
				(error) => {
					this.setState({
						isLoaded: true,
						error: error // error
					});
				}
			);
	}
	render() {
		let {error, isLoaded}=this.state;

		if(error){
			return (<div>Error: {error.message}</div>);
		}

		if(!isLoaded){
			return (<div>Loading ...</div>);
		}
		else{
			return(
				<UIScript prop_value={this.state.data}></UIScript>
			);			
		}
	}
	
}

export default App;
