import React, { Component } from 'react';

class Page1 extends Component {
	constructor(props){
		super(props);
		this.state={
			introduceData: props.introduce_value,
			myphotoData: props.myphoto_value
		}
	}
	render(){
		let introduceData=this.state.introduceData;
		let myphotoData=this.state.myphotoData;

		let {h3, h3span, p, span}=introduceData;
		let {myurl, myalt}=myphotoData;
		return(
			<section id="page1" className="introduce">
				<div className="text">
					<h3>{h3}<br /><span>{h3span}</span></h3><br /><br />
					<p>{p}<br />
					<span>{span}</span></p>
				</div>
				<a href="" className="my_photo"><img src={"./assets/images/"+myurl} alt={myalt} />
					<span>Resume(pdf)</span>
					<div className="dim"></div>
				</a>
			</section>
		);
	}
}

export default Page1;