import React, {Component} from 'react';

class Header extends Component{
	constructor(props){
		super(props);
		this.state={
			logoData: props.logo_value,
			gnbData: props.gnb_value,
		}
	}
	render(){
		let gnbData=this.state.gnbData;
		let gnbList=[];

		for(let i=0; i<gnbData.length; i++){
			// <li><a href="">HOME</a></li>
			gnbList.push(<li key={i+1}><a href="">{gnbData[i]}</a></li>);
		}

		let logoData=this.state.logoData;

		return(
			<header id="header">
				<div className="logo"><h1><a href="">{logoData}</a></h1></div>
				<div className="header_right">
					<nav id="gnb">
						<ul>
							{gnbList}
						</ul>
					</nav>
					<a href="" className="tab">
						<span></span>
						<span></span>
						<span></span>
					</a>
				</div>
			</header>
		);
	}
}

export default Header;