import React, { Component } from 'react';
import Project from './Project';
import Footer from './Footer';

class Portfolio extends Component{
	constructor(props){
		super(props);
		this.state={
			projectData: props.project_value,
			footerData: props.footer_value
		}
	}
	render(){
		return(
			<section id="portfolio">
				<div className="inner">
					<div className="project_label">
						<h2>PORTFOLIO</h2>
					</div>
					<Project project_value={this.state.projectData.visitbusan}></Project>
					<Project project_value={this.state.projectData.morningglory}></Project>
					<Project project_value={this.state.projectData.wylie}></Project>
				</div>
				<Footer footer_value={this.state.footerData}></Footer>
			</section>
		);
	}
}

export default Portfolio;