import React, {Component} from 'react';

class AboutMe extends Component{
	constructor(props){
		super(props);
		this.state={
			photoData: props.photo_value,
			profileData: props.profile_value,
			skillData: props.skill_value
		}
	}
	render(){
		let photoData=this.state.photoData;
		let {url, alt}=photoData;
		let profileData=this.state.profileData;
		let profileDataList=[];

		let skillData=this.state.skillData;
		let skillDataList=[];

		for(let i=0;i<profileData.length;i++){
			let {subtitle, p}=profileData[i];
			profileDataList.push(
				<dd key={i+1}><p className="subtitle">{subtitle}</p><p>{p}</p></dd>
			);
		}

		for(let i=0; i<skillData.length; i++){
			let {p, span1, span2, span3}=skillData[i];

			if(span2 !== undefined && span3 !== undefined){
				skillDataList.push(
					<dd key={p}>
						<p className="subtitle">{p}</p>
						<p>
							<span>{span1}</span>
							<span>{span2}</span>
							<span>{span3}</span>
						</p>
					</dd>
				);
			}
			else if(span2 !== undefined){
				skillDataList.push(
					<dd key={p}>
						<p className="subtitle">{p}</p>
						<p>
							<span>{span1}</span>
							<span>{span2}</span>
						</p>
					</dd>
				);
			}
			else{
				skillDataList.push(
					<dd key={p}>
						<p className="subtitle">{p}</p>
						<p>
							<span>{span1}</span>
						</p>
					</dd>				
				);
			}
		}

		return(
			<section id="aboutme">
				<div className="title">
					<h2>ABOUT</h2>
				</div>
				<div className="content">
					<div className="profile">
						<div className="photo"><img src={"./assets/images/"+url} alt={alt} /></div>
						<div className="text">
							<dl>
								<dt>PROFILE</dt>
								{profileDataList}
							</dl>
						</div>
					</div>
					<div className="skills">
						<dl>
							<dt>SKILLS</dt>
							{skillDataList}
						</dl>
					</div>
				</div>

			</section>
		);
	}
}

export default AboutMe;
