import React, { Component } from 'react';

class Footer extends Component {
	constructor(props){
		super(props);
		this.state={
			footerData: props.footer_value
		}
	}
	render(){
		let footerData=this.state.footerData;
		
		let {url}=footerData.ftvideoData;

		let fttextDataList=[];

		for(let i=0;i<footerData.fttextData.length;i++){
			let {span, p}=footerData.fttextData[i];
			fttextDataList.push(
				<li key={i+1}><span>{span}</span><p>{p}</p></li>
			)
		}

		let ftlinkDataList=[];

		for(let i=0;i<footerData.ftlinkData.length;i++){
			let {link}=footerData.ftlinkData[i];
			ftlinkDataList.push(
				<li key={i+1}><a href={link} target="page"></a></li>
			);
		}

	
		return(
			<div id="footer">
				<video id="ft_video" src={"./assets/videos/"+url}></video>
				<div className="ft_inner">
					<div className="title">
						<h2>CONTACT</h2>
					</div>
					<div className="text">
						<ul>
							{fttextDataList}
						</ul>
					</div>
					<div className="link">
						<ul>
							{ftlinkDataList}
						</ul>
					</div>
				</div>
			</div>
		);
	}
}

export default Footer;