import React, { Component } from 'react';

class Speciality extends Component{
	constructor(props){
		super(props);
		this.state={
			specialityData: props.speciality_value
		}
	}
	render(){
		let specialityData=this.state.specialityData;
		let specialityDataList=[];

		for(let i=0; i<specialityData.length; i++){
			let {url, alt, span1, span2, span3, span4, span5, p1, p2}= specialityData[i];

			if(span3 !== undefined && span5 !== undefined){
				specialityDataList.push(
					<li key={alt}>
						<a href="">
							<div className="photo">
								<img src={"./assets/images/"+url} alt={alt} className="mobile" />
							</div>
							<div className="spec_a">
								<p>
									<span>{span1}</span>
									<span>{span2}</span>
									<span>{span3}</span>
									<span>{span4}</span>
									<span>{span5}</span>
								</p>
							</div>
							<div className="spec">
								<p className="spec_t">{p1}</p>
								<p className="spec_d">{p2}</p>
							</div>
						</a>
					</li>					
				);
			}
			else if(span3 !== undefined){
				specialityDataList.push(
					<li key={alt}>
						<a href="">
							<div className="photo">
								<img src={"./assets/images/"+url} alt={alt} className="mobile" />
							</div>
							<div className="spec_a">
								<p>
									<span>{span1}</span>
									<span>{span2}</span>
									<span>{span3}</span>
									<span>{span4}</span>
								</p>
							</div>
							<div className="spec">
								<p className="spec_t">{p1}</p>
								<p className="spec_d">{p2}</p>
							</div>
						</a>
					</li>					
				);
			}
			else{
				specialityDataList.push(
					<li key={alt}> 
						<a href="">
							<div className="photo">
								<img src={"./assets/images/"+url} alt={alt} className="mobile" />
							</div>
							<div className="spec_a">
								<p>
									<span>{span1}</span>
									<span>{span2}</span>
								</p>
							</div>
							<div className="spec">
								<p className="spec_t">{p1}</p>
								<p className="spec_d">{p2}</p>
							</div>
						</a>
					</li>					
				);
			}
		}
		return(
			<section id="speciality">
				<div className="title">
					<h2>SPECIALITY</h2>
				</div>
				<div className="speciality_list">
					<ul>
						{specialityDataList}
					</ul>
				</div>
			</section>
		);
	}
}

export default Speciality;