import React, { Component } from 'react';
import SwiperJS from './SwiperJS';

class Page3 extends Component{
	constructor(props){
		super(props);
		this.state={
			swiperData: props.swiper_value
		}
	}
	render(){
		return(
			<section id="page3" className="my_skills">
				<div className="content">
					<SwiperJS swiper_value={this.state.swiperData}></SwiperJS>
				</div>
			</section>
		);
	}
}

export default Page3;