import React, { Component } from 'react';
import Video from './Video';
import Header from './Header';
import WheelArea from './WheelArea';
import PageDown from './PageDown';
import RightMenu from './RightMenu';
import MobileKeytext from './MobileKeytext.js';
import MobileMenu from './MobileMenu';
import { Swiper } from 'swiper/react';

class Main extends Component {
	constructor(props) {
		super(props);
		this.state={
			videoData: props.video_value,
			myphotoData: props.myphoto_value,
			logoData: props.logo_value,
			gnbData: props.gnb_value,
			mobilegnbData: props.mobilegnb_value,
			introduceData: props.introduce_value,
			aboutData: props.about_value,
			aboutRightData: props.aboutright_value,
			SwiperData: props.swiper_value,
			rightmenuData: props.rightmenu_value,
			photoData: props.photo_value
		}
	}
	render(){
		return(
			<div id="main">
				<Video video_value={this.state.videoData}></Video>
				<Header 
					logo_value={this.state.logoData} 
					gnb_value={this.state.gnbData}>
				</Header>
				<div className="pdf_down"><a href=""><span>Resume(pdf)</span></a></div>
				<a href="" className="m_pdf_down"><span>Resume(pdf)</span></a>
				<WheelArea
					introduce_value={this.state.introduceData}
					myphoto_value={this.state.myphotoData}
					about_value={this.state.aboutData}
					aboutright_value={this.state.aboutRightData}
					swiper_value={this.state.SwiperData}
					photo_value={this.state.photoData}>
				</WheelArea>
				<PageDown></PageDown>
				<RightMenu
					rightmenu_value={this.state.rightmenuData}>						
				</RightMenu>
				<MobileKeytext
					introduce_value={this.state.introduceData}>					
				</MobileKeytext>
				<MobileMenu
					mobilegnb_value={this.state.mobilegnbData}>						
				</MobileMenu>
				
			</div>
		);
	}
}

export default Main;