import React, { Component } from 'react';

class Page2 extends Component{
	constructor(props){
		super(props);
		this.state={
			aboutData: props.about_value,
			aboutRightData: props.aboutright_value,
			photoData: props.photo_value
		}
	}
	render(){
		let aboutData= this.state.aboutData;
		let {Name, Birth, Phone, Email}=aboutData;

		let aboutRightData=this.state.aboutRightData;
		let aboutRightDataList=[];

		let photoData=this.state.photoData;
		let {url, alt}=photoData;

		for(let i=0;i<aboutRightData.length;i++){
			aboutRightDataList.push(<li key={i+1}>{aboutRightData[i]}</li>)
		}
		console.log(aboutRightData);
		console.log(aboutRightDataList);
		return(
			<section id="page2" className="about_me">
				<div className="content">
					<div className="photo"><img src={"./assets/images/"+url} alt={alt} /></div>
					<div className="text_box">
						<div className="left_text">
							<ul>
								<li><span>Name</span> {Name}</li>
								<li><span>Birth</span> {Birth}</li>
								<li><span>Phone</span> {Phone}</li>
								<li><span>E-mail</span> {Email}</li>
							</ul>
						</div>
						<div className="right_text">
							<ul>
								{aboutRightDataList}
							</ul>
						</div>
					</div>
				</div>
			</section>
		);
	}
}

export default Page2;