import React, { Component } from 'react';

class RightMenu extends Component {
	constructor(props){
		super(props);
		this.state={
			rightmenuData: props.rightmenu_value
		}
	}
	
	render(){
		let rightmenuData=this.state.rightmenuData;
		let rightmenuDataList=[];

		for(let i=0;i<rightmenuData.length;i++){
			let {link, classname}=rightmenuData[i];
			rightmenuDataList.push(
				
				<li key={i+1}><a href={link} className={classname} target="page"></a></li>
			);
		}

		return(
			<div className="right_menu">
				<ul>
					{rightmenuDataList}
				</ul>
			</div>
		);
	}
	
}

export default RightMenu;