import React, {Component} from 'react';

class Video extends Component{
	constructor(props){
		super(props);
		this.state={
			videoData: props.video_value
		}
	}
	componentDidMount(){
		// video interface
		let main_video=document.getElementById("main_video");
		let ft_video=document.getElementById("ft_video");
		main_video.addEventListener("loadeddata", function(){
			main_video.muted=true;
			main_video.play();
		});
		main_video.addEventListener("ended", function(){
			main_video.currentTime=0;
			main_video.play();
		})
		ft_video.addEventListener("loadeddata", function(){
			ft_video.muted=true;
			ft_video.play();
		});
		ft_video.addEventListener("ended", function(){
			ft_video.currentTime=0;
			ft_video.play();
		});
	}
	render(){
		let videoData=this.state.videoData;
		let {url, poster}=videoData;
		return(
			<video id="main_video" src={"./assets/videos/"+url} poster={"./assets/images/"+poster}></video>
		);
	}
}

export default Video;