import React, { Component } from 'react';

class MobileKeytext extends Component {
	constructor(props){
		super(props);
		this.state={
			introduceData: props.introduce_value
		}
	}
	render(){
		let introduceData=this.state.introduceData;
		let {h3, h3span, p, span}=introduceData;
		return(
			<div className="mobile_keytext">
				<h3>{h3}<br /><span>{h3span}</span></h3><br /><br />
				<p>{p}<br />
				<span>{span}</span></p>
			</div>
		);
	}
	
}

export default MobileKeytext;