import React, { Component } from 'react';

class MobileMenu extends Component {
	constructor(props){
		super(props);
		this.state={
			mobilegnbData: props.mobilegnb_value
		}
	}
	render(){
		let mobilegnbData=this.state.mobilegnbData;
		let mobilegnbDataList=[];

		for(let i=0;i<mobilegnbData.length;i++){
			mobilegnbDataList.push(
				<li key={i+1}><a href="">{mobilegnbData[i]}</a></li>
			)
		}
		return(
			<div className="mobile_menu">
				<ul>
					{mobilegnbDataList}
				</ul>
			</div>
		);
	}
	
}

export default MobileMenu;