import React, { Component } from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import "swiper/css";
import "swiper/css/pagination";
import { Pagination } from 'swiper';

class SwiperJS extends Component {
	constructor(props){
		super(props);
		this.state={
			swiperData: props.swiper_value
		}
	}
	render(){
		let swiperData=this.state.swiperData;

		let swiperList=[];

		for(let i=0;i<swiperData.length;i++){
			let {title, content}=swiperData[i];

			if(content){
				swiperList.push(
					<SwiperSlide key={i}>
						<div className="icon"></div>
						<div className="title">{title}</div>
						<div className="content">{content}</div>
					</SwiperSlide>
				);
			}			
			else{
				swiperList.push(
					<SwiperSlide key={i}>
						<div className="icon"></div>
						<div className="title">{title}</div>
					</SwiperSlide>
				)
			}
		}

		return(
			<Swiper 
				slidesPerView={2}
				slidesPerGroup={2}
				centeredSlides={false}
				spaceBetween={20}
				pagination={{
					el: ".swiper-pagination",
					clickable: true
				}}
				breakpoints={{
					1000: {
						slidesPerView: 3,
						slidesPerGroup: 3,
					}
				}}
				modules={[Pagination]}
				className="mySwiper">
				<div className="swiper-wrapper">
					{swiperList}
				</div>
				<div className="swiper-pagination"></div>
			</Swiper>
		)
	}
}

export default SwiperJS;